@use "/src/global.scss" as *;

.post-list {
  display: grid;
  gap: 1rem;
  @include desktop {
    grid-template-columns: repeat(2, 1fr);
  }
  @include laptop {
    grid-template-columns: repeat(2, 1fr);
  }
  @include tablet {
    grid-template-columns: repeat(1, 1fr);
  }
  @include mobile {
    grid-template-columns: repeat(1, 1fr);
  }
}

.no-posts{
  display: flex;
  margin: 10px;
  justify-content: center;
  align-items: center;
}